import React from "react"
import PropTypes from "prop-types"
import {ExpandLess, ExpandMore, CheckBox} from '@material-ui/icons';

export default class Drawer extends React.Component {

    static propTypes = {
        specSelectors: PropTypes.object.isRequired,
        getComponent: PropTypes.func.isRequired,
        fn: PropTypes.object.isRequired,
        authSelectors: PropTypes.object.isRequired,
        errSelectors: PropTypes.object.isRequired,
        authActions: PropTypes.object.isRequired,
    }

    constructor(props) {
        super(props)

        // position of window
        this.state = {
            position: 0,
            authorized: false,
            open: true
        }

        this.listenToScroll = this.listenToScroll.bind(this)
        this.toggleAuthorizedOpen = this.toggleAuthorizedOpen.bind(this)
    }

    componentDidMount() {
        window.addEventListener('scroll', this.listenToScroll)
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.listenToScroll)
    }

    listenToScroll() {
        const scrollTop = document.body.scrollTop || document.documentElement.scrollTop

        // Position of Element, window is at
        var minPos = -1
        this.props.specSelectors.taggedOperations().map((tagObj, tag) => {
            var offset = document.getElementById("operations-tag-" + tag) ? document.getElementById("operations-tag-" + tag).offsetTop : 0
            if (minPos === -1) {
                minPos = offset
            } else if ((scrollTop - offset > -(window.innerHeight / 2)) && (offset > minPos)) {
                minPos = offset
            }
        })

        this.setState({
            position: minPos
        })
    }

    toggleAuthorizedOpen() {
        if (this.state.authorized) {
            this.setState((state) => ({
                open: !state.open
            }))
        }
    }

    render() {
        let {
            specSelectors,
            getComponent,
            fn,
            authSelectors,
            authActions,
            errSelectors,
            specActions
        } = this.props

        // every operation
        let taggedOps = specSelectors.taggedOperations()

        const NavLink = getComponent("NavLink")
        const Authorization = getComponent("AuthWrapper")

        let {
            position,
            authorized,
            open
        } = this.state

        return <div className="navigation-wrapper">
            <div className="navigation-section">
                <div className="header" onClick={this.toggleAuthorizedOpen}>
                    {authorized && <CheckBox className="auth-item"/>}
                    <h1>Authorization</h1>
                    {authorized && 
                        (open ? <ExpandLess /> : <ExpandMore />)
                    }
                </div>
                <div className="body">
                    {(!authorized || open) && <div className="body">
                        <Authorization 
                            fn={fn}
                            getComponent={getComponent}
                            authSelectors={authSelectors}
                            specSelectors={specSelectors}
                            errSelectors={errSelectors}
                            authActions={authActions}
                            specActions={specActions}
                        />
                </div>}
                </div>
            </div>

            <div className="navigation-section">
                <div className="header">
                    <h1>API Reference</h1>
                </div>
                <div className="body">
                    {taggedOps.map( (tagObj, tag) => {
                        return <NavLink 
                            key={tag} 
                            tagObj={tagObj} 
                            tag={tag}
                            getComponent={getComponent}
                            specSelectors={specSelectors}
                            position={position}
                        />
                    }).toArray()}
                </div>
            </div>
        </div>
    }
}