// from : https://github.com/swagger-api/swagger-ui/issues/5981
// usage: window.ui.setScheme(scheme), window.ui.setHost(host), window.ui.setBasePath(basePath)

export default function UrlMutatorPlugin(system) {
    return {
        rootInjects: {
            setScheme: (scheme) => {
                const jsonSpec = system.getState().toJSON().spec.json;
                const schemes = Array.isArray(scheme) ? scheme : [scheme];
                const newJsonSpec = Object.assign({}, jsonSpec, { schemes });

                return system.getSystem().specActions.updateJsonSpec(newJsonSpec);
            },
            setHost: (host) => {
                const jsonSpec = system.getState().toJSON().spec.json;
                const newJsonSpec = Object.assign({}, jsonSpec, { host });

                return system.getSystem().specActions.updateJsonSpec(newJsonSpec);
            },
            setBasePath: (basePath) => {
                const jsonSpec = system.getState().toJSON().spec.json;
                const newJsonSpec = Object.assign({}, jsonSpec, { basePath });
                return system.getSystem().specActions.updateJsonSpec(newJsonSpec);
            }
        }
    }
}