import React from "react"
import PropTypes from "prop-types"
import { Info, CheckBox, CheckBoxOutlineBlank } from "@material-ui/icons"

export default class ApiKeyAuth extends React.Component {
    static propTypes = {
        authorized: PropTypes.object,
        getComponent: PropTypes.func.isRequired,
        errSelectors: PropTypes.object.isRequired,
        schema: PropTypes.object.isRequired,
        name: PropTypes.string.isRequired,
        onChange: PropTypes.func
    }

    constructor(props, context) {
        super(props, context)
        let { name, schema } = this.props
        let value = this.getValue()

        this.state = {
            name: name,
            schema: schema,
            value: value,
            showInfo: false
        }

        this.infoToggle = this.infoToggle.bind(this)
    }

    infoToggle() {
        this.setState((state) => ({
            showInfo: !state.showInfo
        }))
    }

    getValue() {
        let { name, authorized } = this.props

        return authorized && authorized.getIn([name, "value"])
    }

    onChange = (e) => {
        let { onChange } = this.props
        let value = e.target.value
        let newState = Object.assign({}, this.state, { value: value })

        this.setState(newState)
        onChange(newState)
    }

    render() {
        let { schema, getComponent, errSelectors, name } = this.props
        const Input = getComponent("Input")
        const InitializedInput = getComponent("InitializedInput")
        const AuthError = getComponent("authError")

        const Markdown = getComponent("Markdown")
        const JumpToPath = getComponent("JumpToPath", true)
        let value = this.getValue()
        let errors = errSelectors.allErrors().filter(err => err.get("authId") === name)

        name = name || schema.get("name")

        return (
            <div className={value ? "auth-simple encode" : "auth-simple"}>
                {
                    value ? <Input type="text" onChange={this.onChange} value={this.state.value} />
                        : <Input type="text" onChange={this.onChange} placeholder={name} />
                }
                {
                    errors.valueSeq().map((error, key) => {
                        return <AuthError error={error}
                            key={key} />
                    })
                }
                <div className="info-wrapper" onMouseEnter={this.infoToggle} onMouseLeave={this.infoToggle}>
                    <Info />
                    {this.state.showInfo &&
                        <div className="info-inner">
                            <h4>
                                <code>{name || schema.get("name")}</code>&nbsp;
                            (apiKey)
                            <JumpToPath path={["securityDefinitions", name]} />
                            </h4>
                            {value && <h6>Authorized</h6>}
                            <Markdown source={schema.get("description")} />
                            <p>Name: <code>{schema.get("name")}</code></p>
                            <p>In: <code>{schema.get("in")}</code></p>
                        </div>
                    }
                </div>
            </div>
        )
    }
}
