import React from "react"
import PropTypes from "prop-types"
import ImPropTypes from "react-immutable-proptypes"
import { Info, CheckBox, CheckBoxOutlineBlank } from "@material-ui/icons"


export default class BasicAuth extends React.Component {
    static propTypes = {
        authorized: PropTypes.object,
        getComponent: PropTypes.func.isRequired,
        schema: PropTypes.object.isRequired,
        onChange: PropTypes.func.isRequired
    }

    constructor(props, context) {
        super(props, context)
        let { schema, name } = this.props

        let value = this.getValue()
        let username = value.username

        this.state = {
            name: name,
            schema: schema,
            value: !username ? {} : {
                username: username
            },
            showInfo: false
        }
        this.infoToggle = this.infoToggle.bind(this)
    }

    infoToggle() {
        this.setState((state) => ({
            showInfo: !state.showInfo
        }))
    }

    getValue() {
        let { authorized, name } = this.props

        return authorized && authorized.getIn([name, "value"]) || {}
    }

    onChange = (e) => {
        let { onChange } = this.props
        let { value, name } = e.target

        let newValue = this.state.value
        newValue[name] = value

        this.setState({ value: newValue })

        onChange(this.state)
    }

    render() {
        let { schema, getComponent, name, errSelectors } = this.props
        const Input = getComponent("Input")
        const AuthError = getComponent("authError")
        const JumpToPath = getComponent("JumpToPath", true)
        const Markdown = getComponent("Markdown")
        let username = this.getValue().username
        let errors = errSelectors.allErrors().filter(err => err.get("authId") === name)

        return (

            <div className="auth-simple">
                <div>
                    {username ? <Input type="text" onChange={this.onChange} value={this.state.username} />
                        : <Input type="text" required="required" name="username" onChange={this.onChange} placeholder="Username" />
                    }
                    {this.state.value ? <Input type="password" onChange={this.onChange} value={this.state.value} />
                        : <Input autoComplete="new-password"
                            name="password"
                            type="password"
                            placeholder="Password"
                            onChange={this.onChange} />
                    }
                </div>
                {
                    errors.valueSeq().map((error, key) => {
                        return <AuthError error={error}
                            key={key} />
                    })
                }
                <div className="info-wrapper" onMouseEnter={this.infoToggle} onMouseLeave={this.infoToggle}>
                    <Info />
                    {this.state.showInfo &&
                        <div className="info-inner">
                            < h4 > Basic authorization<JumpToPath path={["securityDefinitions", name]} /></h4 >
                            {username && <h6>Authorized</h6>}
                            <Markdown source={schema.get("description")} />
                        </div>
                    }
                </div>
            </div>)
    }

    static propTypes = {
        name: PropTypes.string.isRequired,
        errSelectors: PropTypes.object.isRequired,
        getComponent: PropTypes.func.isRequired,
        onChange: PropTypes.func,
        schema: ImPropTypes.map,
        authorized: ImPropTypes.map
    }
}
