import React from "react"
import PropTypes from "prop-types"
import ImPropTypes from "react-immutable-proptypes"
import Im from "immutable"
import { createDeepLinkPath, escapeDeepLinkPath, sanitizeUrl } from "core/utils"

export default class OperationTag extends React.Component {

    static defaultProps = {
        tagObj: Im.fromJS({}),
        tag: "",
    }

    static propTypes = {
        tagObj: ImPropTypes.map.isRequired,
        tag: PropTypes.string.isRequired,
        
        layoutSelectors: PropTypes.object.isRequired,
        layoutActions: PropTypes.object.isRequired,
        
        getConfigs: PropTypes.func.isRequired,
        getComponent: PropTypes.func.isRequired,
        
        children: PropTypes.element,
    }

    render() {
        const {
            tagObj,
            tag,
            children,

            getConfigs,
            getComponent,
            operations
        } = this.props

        let {
            docExpansion,
            deepLinking,
        } = getConfigs()

        const isDeepLinkingEnabled = deepLinking && deepLinking !== "false"

        const Collapse = getComponent("Collapse")
        const Markdown = getComponent("Markdown")
        const DeepLink = getComponent("DeepLink")
        const Link = getComponent("Link")

        let tagDescription = tagObj.getIn(["tagDetails", "description"], null)
        let tagExternalDocsDescription = tagObj.getIn(["tagDetails", "externalDocs", "description"])
        let tagExternalDocsUrl = tagObj.getIn(["tagDetails", "externalDocs", "url"])

        let isShownKey = ["operations-tag", tag]
        let showTag = true

        

        return <div className={showTag ? "opblock-tag-section is-open" : "opblock-tag-section"} >
            <div className="opblock-method-shortcuts">
                {
                    operations.map(op => {
                        let method = op.get("method")
                        let operationId = op.getIn(["operation", "__originalOperationId"]) || op.getIn(["operation", "operationId"]) || op.get("id")
                        let innerKey = ["operations", tag, operationId]
                        let deprecated = op.get("operation").toJS().deprecated
                        
                        return <span 
                            className={`opblock method-shortcut opblock-${deprecated ? "deprecated" : method}`}//{`opblock-method-shortcut-${method}`} 
                            key={innerKey.join("-")}
                            onClick={() => {
                                window.scrollTo({ top: document.getElementById(innerKey.join("-")).offsetTop - 45, left: 0, behavior: 'smooth' })}}
                        >
                            {method.toUpperCase()}
                        </span>
                    })
                }
            </div>

            <h4
                className={!tagDescription ? "opblock-tag no-desc" : "opblock-tag" }
                id={isShownKey.map(v => escapeDeepLinkPath(v)).join("-")}
                data-tag={tag}
                data-is-open={showTag}
            >
                <DeepLink
                    enabled={isDeepLinkingEnabled}
                    isShown={showTag}
                    path={createDeepLinkPath(tag)}
                    text={tag} 
                />
                    { !tagDescription ? <small></small> :
                        <small>
                            <Markdown source={tagDescription} />
                        </small>
                    }
                    <div>
                        { tagExternalDocsDescription && <small>
                            { tagExternalDocsDescription }
                            { tagExternalDocsUrl ? ": " : null }
                            { tagExternalDocsUrl && <Link
                                href={sanitizeUrl(tagExternalDocsUrl)}
                                onClick={(e) => e.stopPropagation()}
                                target="_blank"
                            >
                                {tagExternalDocsUrl}</Link> 
                            }
                        </small>
                        }
                    </div>

            </h4>

            <Collapse isOpened={showTag}>
                {children}
            </Collapse>
        </div>
    }
}
