import React from "react"
import PropTypes from "prop-types"
import { Info } from "@material-ui/icons"

export default class BaseURL extends React.Component {
    static propTypes = {
        specActions: PropTypes.object.isRequired,
        specSelectors: PropTypes.object.isRequired,
        getComponent: PropTypes.func.isRequired
    }


    constructor(props) {
        super(props)
        this.state = {
            showInfo: false,
            url: ""
        }

        this.infoToggle = this.infoToggle.bind(this)
        this.onChange = this.onChange.bind(this)
        this.keyPress = this.keyPress.bind(this)
    }

    infoToggle() {
        this.setState((state) => ({
            showInfo: !state.showInfo
        }))
    }

    onChange = (e) => {
        let value = e.target.value
        this.setState({ url: value })
    }

    keyPress = (e) => {
        if (e.keyCode == 13) {
            console.log(this.state.url)
            window.ui.setHost(this.state.url)
            window.ui.setBasePath("")
        }
    }

    render() {
        let { getComponent } = this.props

        const Input = getComponent("Input")
        const SchemesContainer = getComponent("SchemesContainer", true)

        let {
            showInfo,
            url
        } = this.state

        return (
            <div className="auth-simple">
                {
                    <SchemesContainer />
                }
                {
                    url ? <Input type="text" onChange={this.onChange} value={url} onKeyDown={this.keyPress} />
                        : <Input type="text" onChange={this.onChange} placeholder="System URL" onKeyDown={this.keyPress} />
                }
                <div className="info-wrapper" onMouseEnter={this.infoToggle} onMouseLeave={this.infoToggle}>
                    <Info />
                    {showInfo &&
                        <div className="info-inner">
                            <h1>System URL (used during Try Out)</h1>
                            <h1>Hostname: FQN</h1>
                        </div>
                    }
                </div>
            </div>
        )
    }

}

BaseURL.propTypes = {
    title: PropTypes.any,
    description: PropTypes.any,
    version: PropTypes.any,
    url: PropTypes.string
}
