import React from "react"

export default class Modal extends React.Component {

    constructor(props) {
        super(props)
        this.handleKeyDown = this.handleKeyDown.bind(this)
    }

    handleKeyDown = event => {
        console.log("key")
        if (event.keyCode === ESC_KEY) {
            this.props.handleClose;
        }
    };

    render() {
        let {
            handleClose,
            show,
            children
        } = this.props


        return show && <div className="dialog-ux" onKeyPress={this.handleKeyDown}>
            <div className="backdrop-ux" onClick={handleClose}></div>
            <div className="modal-ux">
                <div className="modal-dialog-ux" >
                    <div className="modal-ux-inner"></div>
                    {children}
                </div>
            </div>
        </div>
    }
}
